import { useContext, useState } from "react";
import { DataContext } from "../../contexts/dataContext";

import "../../styles/UpdateBox.scss";

const UpdateBox = (props) => {
    const { runFetch, setRunFetch } =useContext(DataContext);
    const [updateValue, setUpdateValue] = useState(props.thisUser);
    const [alert, setAlert] = useState("")

    const changeInput = e => {
        // setUpdateValue({...updateValue, [e.target.name]: e.target.value})
        setUpdateValue({...updateValue, userid: props.thisUser.userid, [e.target.name]: e.target.value})
    }

    const onSubmit = e => {
        e.preventDefault();

        fetch(props.transactionUsersUrl, {
            method: "POST",
            headers: {
                "Content-Type": "application/json",
                'Authorization': `Bearer ${localStorage.getItem("token")}`
            },
            body: JSON.stringify({
                ...updateValue, 
                action: "Update"
            })
        })
        .then(res=>res.json())
        .then(data=>{
            console.log(data)
            if (data.result.toLowerCase().indexOf("ok")!==-1 || data.result.toLowerCase().indexOf("success")!==-1) {
                setAlert(data.result.toLowerCase())
                setRunFetch(!runFetch)
                setTimeout(()=>{
                    props.setIsUpdateBtn(false);
                }, 1000)
            }
        })
        .catch(err=>console.error(err))

    }

    return(
        <div className="overlay" id="UpdateBox">
            <form  onSubmit={onSubmit}>
                <div className="x-btn" onClick={()=>props.setIsUpdateBtn(false)}>X</div>
                <div className="personal-info-inputs">
                    <div className="input-div">
                        {/* <label>
                            <p>user id<span>*</span></p>
                            <input name="userid" type="text"  value={updateValue.userid===null ? "" : updateValue.userid} onChange={changeInput} disabled />
                        </label>
                        <label>
                            <p>password<span>*</span></p>
                            <input name="password" type="password"  value={updateValue.password===null ? "" : updateValue.password} onChange={changeInput} required />
                        </label> */}
                        <label>
                            <p>{props.t("user_name")}</p>
                            <input name="username" type="text"  value={updateValue.username===null ? "" : updateValue.username} onChange={changeInput} />
                        </label>
                        <label>
                            <p>{props.t("company")}</p>
                            <input name="company" type="text"  value={updateValue.company===null ? "" : updateValue.company} onChange={changeInput} />
                        </label>
                        <label>
                            <p>{props.t("title")}</p>
                            <input name="title" type="text"  value={updateValue.title===null ? "" : updateValue.title} onChange={changeInput} />
                        </label>
                        <label>
                            <p>email<span>*</span></p>
                            <input name="email" type="email"  value={updateValue.email===null ? "" : updateValue.email} onChange={changeInput} required />
                        </label>
                        <label>
                            <p>{props.t("birthday")}</p>
                            <input name="birthday" type="date"  value={updateValue.birthday===null ? "" : updateValue.birthday} onChange={changeInput} />
                        </label>
                        <label>
                            <p>{props.t("gender")}</p>
                            <select name="gender"  value={updateValue.gender===null ? "" : updateValue.gender} onChange={changeInput} >
                                <option value=""></option>
                                <option value="m">{props.t("male")}</option>
                                <option value="f">{props.t("female")}</option>
                            </select>                        
                        </label>
                        <label>
                            <p>{props.t("tel")}</p>
                            <input name="telephone" type="tel"  value={updateValue.telephone===null ? "" : updateValue.telephone} onChange={changeInput} pattern="^[0-9-+\s()]*$" />
                        </label>
                        <label>
                            <p>{props.t("mobile")}</p>
                            <input name="mobile" type="tel"  value={updateValue.mobile===null ? "" : updateValue.mobile} onChange={changeInput} pattern="^[0-9-+\s()]*$" /> 
                        </label>
                    </div>
                        <div className="address-div">
                            <label><p>{props.t("zipcode")}</p>
                                <input name="zipcode" type="text" value={updateValue.zipcode===null ? "" : updateValue.zipcode} onChange={changeInput} />
                            </label>
                            <label><p>{props.t("address")}</p>
                                <input name="address" type="text" value={updateValue.address===null ? "" : updateValue.address} onChange={changeInput} /> 
                            </label>              
                        </div>    
                    </div>
                    <input type="submit" value={props.t("confirm")}/>
                    {
                        alert.length>0 && <h1 style={{color: alert.indexOf("ok")!==-1 ? "var(--green)" : "var(--red)"}}>{alert.indexOf("ok")!==-1 ? props.t("update_success") : alert}</h1>
                    }
                </form>            
        </div>

    )
}

export default UpdateBox;
