import React, { useContext, useEffect, useState } from "react";
import { useTranslation } from "react-i18next";

import "../../styles/Dashboard.scss"

import { AiFillAccountBook, AiOutlineGlobal } from "react-icons/ai"
import { InitContext } from "../../contexts/initContext"
import { DataContext } from "../../contexts/dataContext";

function Dashboard (props) {
    const { transactionCheckURL } = useContext(InitContext);
    const { thisUser, setThisUser } = useContext(DataContext);
    const { t, i18n } = useTranslation();
    const [isAction, setIsAction] = useState(false);
    const [alert, setAlert] = useState("")
    const [inputValue, setInputValue] = useState({})
    const [allUrls, setAllUrls] = useState([])

    const clickActionCard = e => setIsAction(!isAction)

    const changeInput = e => {
        setInputValue({...inputValue, [e.target.name]: e.target.value})
    }

    useEffect(()=>{
        console.log(thisUser.usersseqno)
        if(typeof(thisUser.usersseqno)!==String) {
            console.log("return")
            return
        }
        fetch(transactionCheckURL, {
            method: "POST",
            headers: {
                "Content-Type": "application/json",
                'Authorization': `Bearer ${localStorage.getItem("token")}`
            },
            body: JSON.stringify({
                // "action": "Query",
                // "usersseqno": thisUser.usersseqno,
                // "pageurl": "",
                // "noticemail": thisUser.email
                "action": "Query",
                "usersseqno": 10,
                "pageurl": "",
                "noticemail": thisUser.email
            })
        })
        .then(res => res.json())
        .then(data =>{
            console.log(data)
            setAllUrls(data)
            // setAlert(data.result)
        })
        .catch(err => console.error(err))

    // eslint-disable-next-line react-hooks/exhaustive-deps
    }, [])

    const onSubmit = e => {
        e.preventDefault()

        fetch(transactionCheckURL, {
            method: "POST",
            headers: {
                "Content-Type": "application/json",
                'Authorization': `Bearer ${localStorage.getItem("token")}`
            },
            body: JSON.stringify({
                "action" : "Add",
                "usersseqno" : 3,
                "pageurl": "https://www.104.com.tw/company/1a2x6bllid",
                "noticemail": "ningsen.cheng1@cybersecthreat.com"    
            })
        })
        // .then(res=>res.json())
        .then(res=>{
            // console.log(res)
            return res.json()
        })
        .then(data=>{
            console.log(data)
            // alert(data.result)
            // setAlert(data.result)
        })
        .catch(err => console.error(err))
    }
    

    return (
        <>
        <main id="Dashboard">
            <h1>{t("welcome")}</h1>
            <section>
                <p>welcome {thisUser.userid}</p>
                <p>{thisUser.email}</p>
                <p>seqno: <span>{thisUser.seqno}</span></p>
                <p>usersseqno: <span>{thisUser.usersseqno}</span></p>
                <div className="action-card" onClick={clickActionCard}>
                    <div className="top-div">
                        <AiOutlineGlobal/>
                        <h1>{t("monitor_web_content.title")}</h1>
                    </div>
                    <p>{t("monitor_web_content.text")}</p>
                    {
                        allUrls.length>0 ?
                        allUrls.map(url=><ul>
                                <li>seqno: <span>{url.seqno}</span></li>
                                <li>usersseqno: <span>{url.usersseqno}</span></li>
                                <li>pageurl: <span>{url.pageurl}</span></li>
                                <hr/>
                            </ul>):
                        <h1>no url, add url?</h1>
                    }
                    <button >add url</button>    
                </div>
                <div style={{display: isAction ? "block" : "none"}}>
                    <form onSubmit={onSubmit}>
                        <label>url:
                            <input type="url" name="pageurl" value={inputValue.pageurl} onChange={changeInput} required/>
                        </label>
                        
                        <input type="submit" value="submit" />
                        {
                            alert.length>0 && <p style={{color: alert.toLowerCase().indexOf("ok")!==-1 ? "green" : "red" }}>{alert}</p>
                        }
                    </form>
                </div>  
                         
            </section>
        </main>
        </>
    )
}

export default Dashboard;